<template>
  <div v-show="show" :style="{left:mouseRight.x,top:mouseRight.y}" class="tabar">
    <div @click="addmenu">新增目录</div>
    <div v-if="canshow(1)" @click="updatemenu">修改目录</div>
    <div v-if="canshow(2)" @click="delmenu">删除目录</div>
    <div @click="addlink">新增链接</div>
    <div v-if="canshow(3)" @click="dellink">删除链接</div>
    <div v-if="canshow(4)" @click="updatelink">修改链接</div>
    <div v-if="canshow(5)" @click="delhistory">删除记录</div>
    <div v-if="canshow(6)" @click="swiptBg">换个背景</div>
    <div v-if="canshow(6)" @click="downbg">下载壁纸</div>
    <div @click="setting">个性设置</div>
  </div>
</template>

<script>
import {memory} from "jsb-util";
import bus from "@/util/bus";
import {delete_link, delete_menu} from "@/api";

export default {
  name: "mouse",
  data() {
    return {
      mouseRight: {
        x: 0,
        y: 0
      },
      show: false,
      info: {},
      ctype: [],
      element: null
    }
  },
  props: ["xy", "type"],
  methods: {
    addmenu() {
      bus.emit("menushow", true)
    },
    downbg() {
      const data = base64ToBlob(memory.get("bg"));
      let a = document.createElement("a");
      a.href = URL.createObjectURL(data);
      a.download = "壁纸.jpg"
      a.click();
    },
    updatemenu() {
      const list = memory.get("list") || []
      const index = list.findIndex(e => e.id == this.info)
      if (index === -1) return false;
      const info = list[index];
      bus.emit("menushow", info)
      this.show = false
    },
    addlink() {
      bus.emit("show", true)
    },
    delhistory() {
      bus.emit("delhistory", this.info)
      this.show = false
    },
    swiptBg() {
      let data = new FileReader()
      data.onload = (er) => {
        const base = data.result;
        if (base.length < 100) {
          this.$notify.error({
            title: '出现异常',
            message: '没有切换成功'
          });
        }
        memory.set("bg", base)
      }
      this.$notify.info({
        title: '切换中',
        message: '请稍等片刻，我处理一下'
      });
      fetch("https://web.png.ink/background.php").then(ell => {
        ell.blob().then(cc => {
          data.readAsDataURL(cc);
          const ac = URL.createObjectURL(cc)
          document.querySelector("#root").style.background = `url(${ac}) no-repeat center/cover`
        })
      })
      this.show = false
    },
    async delmenu() {
      try {
        await this.$confirm("是否删除？")
      } catch (e) {
        return false;
      }
      const {code} = await delete_menu({id: this.info});
      if (code === 0) {
        this.element.parentNode.remove()
        this.$message.success("删除成功")
      } else {
        this.$message.error("删除失败")
      }
      this.show = false
    },
    async dellink() {
      try {
        await this.$confirm("是否删除？")
      } catch (e) {
        return false;
      }
      const lists = memory.get("lists") || []
      const index = lists.findIndex(e => e.id == this.info)
      if (index === -1) return false;
      const info = lists[index];
      const {code} = await delete_link({id: info.id});
      if (code === 0) {
        this.element.remove()
        this.$message.success("删除成功")
      } else {
        this.$message.error("删除失败")
      }
      this.show = false
    },
    setting() {
      bus.emit("settings")
      this.show = false;
    },
    updatelink() {
      const lists = memory.get("lists") || []
      const index = lists.findIndex(e => e.id == this.info)
      if (index === -1) return false;
      const info = lists[index];
      bus.emit("showandupdate", info)
      this.show = false
    },
    canshow(index) {
      return Boolean(this.ctype.indexOf(index) > -1)
    }
  },
  mounted() {
    document.addEventListener("click", (event) => {
      this.show = false;
    })
    const touch = {
      isdown: false,
      time: null,
    }
    window.addEventListener("touchstart", (e) => {
      touch.isdown = true;
      touch.time = new Date().getTime();
      touch.time = setTimeout(_ => {
        this.mouseRight = {
          x: e.changedTouches[0].clientX + 'px',
          y: e.changedTouches[0].clientY + 'px'
        }
        try {
          let t = JSON.parse(e.target.attributes.ctype.value)
          if (t.length > 0) {
            this.ctype = t;
          }
        } catch (e) {
          this.ctype = []
        }
        try {
          let info = e.target.attributes.cdata.value;
          this.info = info
          this.element = e.target
        } catch (e) {
        }
        this.show = true;
      }, 500)
    })
    window.addEventListener("touchend", function () {
      touch.isdown = false;
      clearTimeout(touch.time)
      touch.time = null;

    })

    window.addEventListener("mousedown", (e) => {
      if (e.button === 2) {
        this.mouseRight = {
          x: e.clientX + 'px',
          y: e.clientY + 'px'
        }
        try {
          let t = JSON.parse(e.toElement.attributes.ctype.value)
          if (t.length > 0) {
            this.ctype = t;
          }
        } catch (e) {
          this.ctype = []
        }
        try {
          let info = e.toElement.attributes.cdata.value;
          this.info = info
          this.element = e.target
        } catch (e) {

        }
        this.show = true;
        e.preventDefault()
        e.stopPropagation();
      }
    }, {passive: false})
  },
}
</script>

