<template>
  <div class="background">
    <div class="upbutton" mauto>
      <img src="@/assets/img/upfile.svg"/>
      <span class="upbuttontext" hover>点击上传10MB以内JPEG/PNG图片</span>
      <input cli multiple="multiple" type="file" @change="uplist"/>
    </div>
  </div>
</template>

<script setup>
import {memory} from "jsb-util";
import bus from "@/util/bus";

const uplist = (event) => {
  const file = event.target.files;
  if (file.length > 0) {
    let f = file[0];
    let fr = new FileReader();
    fr.onload = () => {
      memory.set('bg', fr.result)
      bus.emit('set_background')
    }
    fr.readAsDataURL(f)
  }
}
</script>

